                    <ul>- Correction du background pour le stream</ul>
                </div>
                <div>
                    <h1>V3.1.8</h1>
                    <ul>- Correction d'un bug lors de la suppression d'un bingo. Merci à Seinsei pour ton retour</ul>
                </div>
