                    <ul>- Retrait d'une alerte oubliée</ul>
                    <ul>- Amélioration de la réactivité des boutons suite à une MAJ des dépendances</ul>
                </div>
                <div>
                    <h1>V3.1.1 Less But More</h1>
                    <ul>- Abandon de vue-js-modal</ul>
                    <ul>- Pour, à la place, BootStrap-Modal for Vue-js</ul>
                    <ul>- Les bouttons de l'édition bug moins, toujours pas trouvé la raison (Il semble que stoper la propagation a amélioré la situation)</ul>
