                </div>
                <div>
                    <h1>V3.1.0 Dimension</h1>
                    <ul>- Ajout de la fonctionnalité "Gradation"</ul>
                    <ul>- Click gauche pour valider une case et droit pour invalider</ul>
                    <ul>- Edit d'une case qui a changé, quelque bug sur les bouttons pour une raison que j'ignore</ul>
