                    <ul>- Stream mode. Background en bleu, texte en blanc avec contour noir pour les faire ressortir (Un
                        peu comme les sous-titres)</ul>
                </div>
                <div>
                    <h1>V2.8</h1>
                    <ul>- Correction du bug "Multi input" de la 2.7</ul>
