        </b-modal>
        <b-modal id="tuto">
            <div style="text-align: center;">
                <ul>Premièrement il faut ajouter des lignes/colonnes. Ensuite pour éditer une case il suffit de double
                    cliquer et appuyer sur ENTER</ul>
                <ul>Il est possible de faire un logo en laissant le champ vide.</ul>
                <ul>Une fois l'édition terminée il faut passer en mode BINGO !</ul>
            </div>
