<script>
    import firebase from 'firebase/app'
    import 'firebase/auth';

    export default {
        name: "Navbar",
        data: function () {
            return {
                email: '',
                password: '',
                nbTent: 0,
            }
        },
        methods: {
            newsSwap() {
                this.$bvModal.show('news')
            },
            tutoSwap() {
                this.$bvModal.show('tuto')
            },
            login(){
                let lthis = this;
                if (this.nbTent <= 2) {
                    firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
                        function (user) {
                            lthis.$store.state.isAuth = true;
                        },
                        function (error) {
                            alert(error)
                        }
                    );
                    lthis.nbTent = this.nbTent + 1;
                } else {
                    alert("Trop de tentatives. 10 min de TO");
                }
            },
            logout() {
                let lthis = this;
                firebase.auth().signOut().then(
                    function () {
                        lthis.$store.state.isAuth = false;
                    }
                )
            },
        },
    }
</script>

<style scoped>
    nav {
        margin-bottom: 2rem;
    }

    .titre:hover {
        transform: scale(1.15);
    }
</style>
